export function errorMessageFormatter(err: any): string {
  if (typeof err === 'string') {
    return err;
  }

  const status: any = {
    '-1': 'Servidor no encontrado',
    400: 'Dados invlidos',
    401: 'Sem permisso de acesso',
    403: 'Sem permisso de acesso',
    404: 'No encontrado',
    422: 'Dados invlidos'
  };

  switch ((err || {}).message) {
    case 'no-internet':
    case 'NETWORK_ERROR':
      return 'Sem conexo com a internet';
    case 'zipcode-not-found':
      return 'CEP no encontrado';
    case 'api-error':
      if (err.status === -1) {
        return 'No conseguimos se comunicar com o servidor';
      }

      return status[err.status] || 'Algo deu errado...';
    default:
      return 'Algo deu errado...';
  }
}
